import React from "react";
import { Card } from "./Card";
import s from "./Days.module.scss";
import { Tabs } from "./Tabs";

type Props = {};

export interface Day {
  day: string;
  day_info: string;
  icon_id: string;
  temp_day: string;
  temp_night: string;
  info: string;
}
export const Days = (props: Props) => {
  const days: Day[] = [
    {
      day: "",
      day_info: "28 ",
      icon_id: "sun",
      temp_day: "+18",
      temp_night: "+15",
      info: "",
    },
    {
      day: "",
      day_info: "29 ",
      icon_id: "small_rain_sun",
      temp_day: "+18",
      temp_night: "+15",
      info: "   ",
    },
    {
      day: "",
      day_info: "30 ",
      icon_id: "small_rain",
      temp_day: "+18",
      temp_night: "+15",
      info: " ",
    },
    {
      day: "",
      day_info: "28 ",
      icon_id: "mainly_cloudy",
      temp_day: "+18",
      temp_night: "+15",
      info: "",
    },
    {
      day: "",
      day_info: "28 ",
      icon_id: "rain",
      temp_day: "+18",
      temp_night: "+15",
      info: "",
    },
    {
      day: "",
      day_info: "28 ",
      icon_id: "sun",
      temp_day: "+18",
      temp_night: "+15",
      info: "",
    },
    {
      day: "",
      day_info: "28 ",
      icon_id: "sun",
      temp_day: "+18",
      temp_night: "+15",
      info: "",
    },
  ];
  return (
    <>
      <Tabs />
      <div className={s.days}>
        {days.map((day) => (
          <Card key={day.day_info + " " + day.day} dayItem={day} />
        ))}
      </div>
    </>
  );
};
